const { Component } = React

const BigNumber = ({ children }) => 
  <div className="big-number">
    {children}
    <style jsx>{`
      .big-number {
        font-family: Helvetica;
        font-size: 20rem;
        color: white;
      }
    `}</style>
  </div>

class Page extends Component {
  
  state = { scrollPosition: 0 } 

  componentDidMount() {
    setInterval(() => {
      this.setState(s => ({ scrollPosition: s + 1 }))
    }, 1000)
  }

  handleScroll = e => {
  }
 
  render(){
    return ( 
      <div className="root">
        <BigNumber>{this.state.scrollPosition}</BigNumber>
        <style jsx>{`
          .root {
            background: #000;
            height: 100vh;
            width: 100vw;
            display: flex;
            justify-content: center;
            align-items: center;
          }
        `}</style>
      </div>
    )
  }
}

export default Page



